import { css, modifier, styled, variants } from '@lokalise/styled';

import type { Appearance } from './IconButton';
import { spacing, value } from '../../utils/theme';
import { Pressable } from '../Pressable';

export type Size = 'xs' | 'sm' | 'md' | 'lg';

type IconButtonProps = {
	$appearance: Appearance;
	$size: Size;
	$active?: boolean;
	disabled?: boolean;
	$isLoading: boolean;
};

const variantColorStyles = css<IconButtonProps>`
	${variants('$appearance', {
		primary: css`
			color: ${value('color.text.default')};

			// hover
			&:hover:not([disabled]) {
				background-color: ${value('color.background.action.secondary.hover')};
				color: ${value('color.text.link.hover')};
			}

			// pressed
			&:active:not([disabled]),
			&:focus:active:not([disabled]) {
				background-color: ${value('color.background.action.secondary.hover')};
				color: ${value('color.text.link.hover')};
			}

			// focus
			&:focus-visible:not([disabled]) {
				background-color: ${value('color.background.action.secondary.hover')};
				color: ${value('color.text.link.hover')};
			}

			// activated
			${modifier(
				'$active',
				css`
					background-color: ${value('color.background.active')};
					color: ${value('color.text.active')};
				`,
			)}
		`,
		secondary: css`
			color: ${value('color.text.default')};

			// hover
			&:hover:not([disabled]) {
				background-color: ${value('color.background.action.tertiary.hover')};
				color: ${value('color.text.default')};
			}

			// pressed
			&:active:not([disabled]),
			&:focus:active:not([disabled]) {
				background-color: ${value('color.background.action.tertiary.hover')};
				color: ${value('color.text.default')};
			}

			// focus
			&:focus-visible:not([disabled]) {
				background-color: ${value('color.background.action.tertiary.hover')};
				color: ${value('color.text.default')};
			}

			// activated
			${modifier(
				'$active',
				css`
					background-color: ${value('color.background.active')};
					color: ${value('color.text.active')};
				`,
			)}
		`,
		verified: css`
			color: ${value('color.text.default')};

			// hover
			&:hover:not([disabled]) {
				background-color: ${value('color.background.warning')};
				color: ${value('color.text.default')};
			}

			// pressed
			&:active:not([disabled]),
			&:focus:active:not([disabled]) {
				background-color: ${value('color.border.warning')};
				color: ${value('color.text.default')};
			}

			// focus
			&:focus-visible:not([disabled]) {
				background-color: ${value('color.background.warning')};
				color: ${value('color.text.default')};
			}

			// activated
			${modifier(
				'$active',
				css`
					background-color: ${value('color.background.warning')};
					color: ${value('color.text.default')};
				`,
			)}
		`,
		reviewed: css`
			color: ${value('color.text.default')};

			// hover
			&:hover:not([disabled]) {
				background-color: ${value('color.background.success')};
				color: ${value('color.text.default')};
			}

			// pressed
			&:active:not([disabled]),
			&:focus:active:not([disabled]) {
				background-color: ${value('color.border.success')};
				color: ${value('color.text.default')};
			}

			// focus
			&:focus-visible:not([disabled]) {
				background-color: ${value('color.background.success')};
				color: ${value('color.text.default')};
			}

			// activated
			${modifier(
				'$active',
				css`
					background-color: ${value('color.background.success')};
					color: ${value('color.text.default')};
				`,
			)}
		`,
	})}
`;

const variantSizeStyles = css<IconButtonProps>`
	${variants('$size', {
		xs: css`
			height: ${value('size.height.iconbutton.extra-small')};
			padding: ${spacing(1)};
			font-size: calc(${value('size.height.iconbutton.extra-small')} - ${spacing(2)});
		`,
		sm: css`
			height: ${value('size.height.iconbutton.small')};
			padding: ${spacing(1)};
			font-size: calc(${value('size.height.iconbutton.small')} - ${spacing(2)});
		`,
		md: css`
			height: ${value('size.height.iconbutton.default')};
			padding: ${spacing(1)};
			font-size: calc(${value('size.height.iconbutton.default')} - ${spacing(2)});
		`,
		lg: css`
			height: ${value('size.height.iconbutton.large')};
			padding: ${spacing(1)};
			font-size: calc(${value('size.height.iconbutton.large')} - ${spacing(2)});
		`,
	})}
`;

export const Container = styled(Pressable)<IconButtonProps>`
	display: inline-flex;
	align-items: center;
	background-color: transparent;
	transition:
		background-color ${value('motion.speed.1')},
		color ${value('motion.speed.1')};
	border: 0;
	border-radius: ${value('radius.iconbutton')};
	text-decoration: none;

	${variantColorStyles}
	${variantSizeStyles}

	// disabled
    &:disabled {
		cursor: not-allowed;
		color: ${value('color.text.disabled')};

		${modifier(
			'$isLoading',
			css<IconButtonProps>`
				color: initial;
				cursor: auto;
				${variantColorStyles}
			`,
		)}
	}
`;
