import type { PressableProps } from '../Pressable';
import { Pressable } from '../Pressable';

export const CloseIconButton = styled(IconButton)<IconButtonProps>`
	opacity: 0;
	transition: opacity 0.2s;
`;

export const TypeIcon = styled.div.withConfig({
	shouldForwardProp: excludeProps(['type']),
})`
	flex-basis: 16px;
	flex-shrink: 0;
	flex-grow: 0;
	height: 20px;
	display: flex;
	align-items: center;
`;

export const Content = styled.div`
	width: 100%;
	flex-shrink: 1;
	flex-grow: 1;
	display: grid;
	grid-template-columns: 1fr;
	gap: ${spacing(1)};
`;

export const Title = styled.span`
	${typography('body.caption.strong')};
	display: -webkit-box;
	text-overflow: ellipsis;
	-webkit-line-clamp: 2;
	-webkit-box-orient: vertical;
	word-break: normal;
	overflow: hidden;
`;

export const Description = styled.span`
	${typography('body.caption.default')};
	display: -webkit-box;
	text-overflow: ellipsis;
	-webkit-line-clamp: 8;
	-webkit-box-orient: vertical;
	word-break: normal;
	overflow: hidden;
`;

export const ToastAnchor = styled(Pressable)<PressableProps>`
	${typography('body.link.caption')};
	text-decoration: underline;
	cursor: pointer;
	color: ${value('color.text.default')};

	${modifier(
		({ href }: PressableProps) => !href,
		css`
			background-color: transparent;
			border: none;
			font-family: inherit;
			padding: 0;
		`,
	)}

	&:hover {
		opacity: 0.7;
	}
	transition: opacity ${value('motion.speed.1')};
`;

